import { PlusOutlined } from "@ant-design/icons";
import { Dispatch } from "redux";
import { useDispatch } from "react-redux";

import { Space, Button, Radio, Modal, message } from "antd";

import React, { useEffect, useState } from "react";
import Boolean from "./DataTypes/Boolean";
import Number from "./DataTypes/Number";
import Ref from "./DataTypes/Ref";
import String from "./DataTypes/String";
import Date from "./DataTypes/Date";
import File from "./DataTypes/File";

import {
  ICollection,
  ESchemaTypes,
  ICollectionSchema,
  ERelationShip,
} from "../types/global";
import { updateCollection } from "../store/actionCreators";
import JSON from "./DataTypes/JSON";

function NewCollectionTypeModal({
  selectedCollection,
  editSchema,
}: {
  selectedCollection: ICollection;
  editSchema?: ICollectionSchema;
}) {
  // variables
  const [isModalVisible, setIsModalVisible] = useState(false);

  const error = () => {
    message.error("Type already exist");
  };

  // local state
  const [state, setState] = useState<ICollectionSchema>({
    name: "",
    type: ESchemaTypes.string,
    default: "",
    index: false,
    maxLength: 0,
    minLength: 0,
    required: false,
    trim: false,
    unique: false,
    uppercase: false,
    lowercase: false,
    enum: [],
    ref: "none",
    relationship: ERelationShip.hasMany,
    autopopulate: true,
  });

  const dispatch: Dispatch<any> = useDispatch();

  const saveCollection: any = React.useCallback(
    (collection: ICollection) => dispatch(updateCollection(collection)),
    [dispatch]
  );

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  const resetState = () => {
    setState({
      name: "",
      type: ESchemaTypes.string,
      default: "",
      index: false,
      maxLength: 0,
      minLength: 0,
      required: false,
      trim: false,
      unique: false,
      uppercase: false,
      lowercase: false,
      ref: "none",
      relationship: ERelationShip.hasMany,
      enum: [],
      autopopulate: true,
    });
  };

  useEffect(() => {
    if (!isModalVisible) {
      resetState();
    }
  }, [isModalVisible]);

  useEffect(() => {
    if (editSchema) {
      setState(editSchema);
      setIsModalVisible(true);
    }
  }, [editSchema]);

  const footer = (
    <Button
      type="primary"
      onClick={() => {
        const schemas: any = {
          ...selectedCollection.schema,
        };
        if (!editSchema && schemas[state.name]) {
          error();
          return;
        }

        schemas[state.name] = state;

        const updatedCollection: ICollection = {
          ...selectedCollection,
          schema: schemas,
          fields: Object.keys(schemas).length,
        };
        saveCollection(updatedCollection);
        handleOk();
      }}
      disabled={state.name.length === 0}
    >
      {"Save"}
    </Button>
  );

  return (
    <>
      <button
        className="menu-button"
        onClick={showModal}
        style={{
          width: "100%",
          height: 50,
          textTransform: "uppercase",
          fontWeight: "bold",
        }}
      >
        Add New Field
      </button>
      <Modal
        title={
          <Space>
            {" "}
            <PlusOutlined />
            <Radio.Group
              onChange={(e) => {
                setState((prev: any) => ({ ...prev, type: e.target.value }));
              }}
              value={state.type}
            >
              <Radio.Button value={ESchemaTypes.string}>String</Radio.Button>
              <Radio.Button value={ESchemaTypes.boolean}>Boolean</Radio.Button>
              <Radio.Button value={ESchemaTypes.number}>Number</Radio.Button>
              <Radio.Button value={ESchemaTypes.ref}>Ref</Radio.Button>
              <Radio.Button value={ESchemaTypes.date}>Date</Radio.Button>
              <Radio.Button value={ESchemaTypes.file}>File</Radio.Button>
              <Radio.Button value={ESchemaTypes.json}>JSON</Radio.Button>
            </Radio.Group>
          </Space>
        }
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        footer={footer}
        width={800}
      >
        <String state={state} setState={setState} />
        <Boolean state={state} setState={setState} />

        <Number state={state} setState={setState} />

        <Ref state={state} setState={setState} />
        <Date state={state} setState={setState} />

        <File state={state} setState={setState} />
        <JSON state={state} setState={setState} />
      </Modal>
    </>
  );
}

export default NewCollectionTypeModal;
