import { Modal, message } from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import { ICollection } from "../types/global";

const { confirm } = Modal;

function RemoveCollectionModal({
  collection,
  deleteCollectionAction,
}: {
  collection: ICollection;
  deleteCollectionAction: Function;
}) {
  const deleteCollection = () => {
    deleteCollectionAction(collection);
    message.success("Collection removed");
  };

  return confirm({
    title: "Sure you want to delete this collection?",
    icon: <ExclamationCircleOutlined />,
    content: null,
    onOk() {
      deleteCollection();
    },
    onCancel() {
      console.log("Cancel");
    },
  });
}

export default RemoveCollectionModal;
