import { PageHeader, Card, Space } from "antd";
import { CollectionsState, ICollection } from "../../types/global";

import { useHistory, useParams } from "react-router";
import { shallowEqual, useSelector } from "react-redux";
import queryString from "query-string";
import { useEffect, useState } from "react";
import Text from "antd/lib/typography/Text";
import { crudCollection, EOperations, ICurdBody } from "../../http";

interface IQueryParams {
  action?: string;
  id?: string;
}

function ShowCollectionAction() {
  const history = useHistory();
  const [selectedRecord, setSelectedRecord] = useState<any>();
  const { collection: collectionName }: any = useParams();
  const queryParams: IQueryParams = queryString.parse(history.location.search);
  const selectedCollection: ICollection = useSelector(
    (state: CollectionsState) => state.collections,
    shallowEqual
  )
    .filter((collection) => collection.show)
    .find((c) => c.name === collectionName);

  const getRecordData = async () => {
    try {
      const action: ICurdBody = {
        model: collectionName,
        operations: EOperations.read,
        data: {
          id: queryParams.id,
        },
      };
      const { data } = await crudCollection(action);

      setSelectedRecord(data);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (!selectedRecord && queryParams.id) {
      getRecordData();
    }
  }, [queryParams]);
  return (
    <div style={{ marginBottom: 60 }}>
      {selectedRecord && (
        <>
          <PageHeader
            onBack={() => window.history.back()}
            title={selectedCollection.name}
            style={{ textTransform: "capitalize" }}
            ghost={false}
          />
          <Card style={{ margin: 10 }}>
            <div>
              {Object.keys(selectedRecord).map((key: any) => {
                console.log(selectedRecord[key]);
                return (
                  key !== "key" && (
                    <div>
                      <Space direction="vertical" style={{ width: "100%" }}>
                        <Text
                          style={{ textTransform: "capitalize" }}
                          type="secondary"
                        >
                          {key}
                        </Text>

                        <p>
                          {Array.isArray(selectedRecord[key]) ? (
                            selectedRecord[key].map((data: any) => (
                              <DisplayData data={data} />
                            ))
                          ) : typeof selectedRecord[key] === "object" ? (
                            <DisplayData data={selectedRecord[key]} />
                          ) : (
                            selectedRecord[key].toString()
                          )}
                        </p>
                      </Space>
                      <br />
                    </div>
                  )
                );
              })}
            </div>
          </Card>
        </>
      )}
    </div>
  );
}

const DisplayData = (data: any) => {
  data = data["data"];
  return (
    <div
      style={{
        borderLeft: "6px solid #1890ff",
        border: "1px dotted #1890ff",
        padding: 10,
        margin: 2,
        width: "100%",
      }}
    >
      {typeof data === "string"
        ? data
        : Object.keys(data).map((key: any) => {
            return (
              <>
                <Space direction="vertical">
                  <Text
                    style={{ textTransform: "capitalize" }}
                    type="secondary"
                  >
                    {key}
                  </Text>
                  <p>
                    {typeof data[key] === "object" ? (
                      <DisplayData data={data[key]} />
                    ) : (
                      data[key]
                    )}
                  </p>
                </Space>
                <br />
              </>
            );
          })}
    </div>
  );
};

export default ShowCollectionAction;
