import React, { useState, useEffect } from "react";
import { Space, Input, Checkbox, InputNumber, Row, Col, Tag } from "antd";
import { CheckboxValueType } from "antd/lib/checkbox/Group";
import { ICollectionSchema } from "../../types/global";

import RequiredText from "../common/RequiredText";

const { TextArea } = Input;
const settings: any = {
  index: false,
  required: false,
  trim: false,
  unique: false,
  uppercase: false,
  lowercase: false,
};

const getKeyValue =
  <U extends keyof T, T extends object>(key: U) =>
  (obj: T) =>
    obj[key];

function String({
  state,
  setState,
}: {
  state: ICollectionSchema;
  setState: Function;
}) {
  const [typeSettings, setTypeSettings] = useState<Array<CheckboxValueType>>(
    []
  );

  const [enumText, setEnumText] = useState("");

  const updateState = (key: string, value: any) => {
    const updatedData: any = {};
    updatedData[key] = value;
    setState((prev: ICollectionSchema) => ({ ...prev, ...updatedData }));
  };

  useEffect(() => {
    const getStateSettings = Object.keys(state).filter(
      (key: any) =>
        Object.keys(settings).includes(key) &&
        getKeyValue<keyof ICollectionSchema, ICollectionSchema>(key)(state)
    );
    setTypeSettings(getStateSettings);
    if (state.enum.length) {
      setEnumText(state.enum.join("\n"));
    }
  }, [state]);

  return (
    <>
      {state.type === "string" && (
        <Space direction="vertical" style={{ width: "100%", overflow: "auto" }}>
          <span>Name for the type</span>
          <Input
            placeholder="Type Name"
            onChange={(e) => updateState("name", e.target.value)}
            value={state.name}
          />
          <RequiredText />
          <span>Default value</span>
          <Input
            onChange={(e) => updateState("default", e.target.value)}
            value={state.default}
            placeholder="Keep empty for not setting the value"
          />
          <span>
            Enums <Tag color="geekblue">{enumText.split("\n").length}</Tag>{" "}
          </span>

          <TextArea
            showCount
            rows={10}
            style={{ width: "100%", textTransform: "lowercase" }}
            value={enumText}
            placeholder="Keep empty for not setting the value"
            onChange={(e) => {
              setEnumText(e.target.value.toLowerCase());
            }}
            onKeyDown={(e) => {
              if (e.key === "Enter") {
                updateState("enum", enumText.trim().split("\n"));
              }
            }}
          />
          <span>Settings</span>
          <Checkbox.Group
            style={{
              width: "100%",
              height: "100%",
            }}
            onChange={(e) => {
              setState((prev: ICollectionSchema) => {
                if (!e.includes("maxLength")) {
                  prev["maxLength"] = 0;
                }
                if (!e.includes("minLength")) {
                  prev["minLength"] = 0;
                }
                Object.keys(settings).forEach((key: any) => {
                  if (!["maxLength", "minLength"].includes(key))
                    settings[key] = e.includes(key);
                });
                return { ...prev, ...settings };
              });
              setTypeSettings(e);
            }}
            value={typeSettings}
          >
            <Row>
              <Col lg={12} className="d-flex-col">
                <Checkbox value="required">Required</Checkbox>
                <Checkbox value="index">Index</Checkbox>
                <Checkbox value="unique">Unique</Checkbox>
                <Checkbox
                  value="lowercase"
                  disabled={typeSettings.includes("uppercase")}
                >
                  Lowercase
                </Checkbox>
                <Checkbox
                  value="uppercase"
                  disabled={typeSettings.includes("lowercase")}
                >
                  Uppercase
                </Checkbox>
              </Col>

              <Col lg={12} className="d-flex-col">
                <Checkbox value="trim">Trim</Checkbox>

                <h4>Minimum Length</h4>
                <InputNumber
                  min={0}
                  value={state.minLength || 0}
                  onChange={(number) => updateState("minLength", number)}
                />
                <h4>Maximum Length</h4>

                <InputNumber
                  min={0}
                  value={state.maxLength || 0}
                  onChange={(number) => updateState("maxLength", number)}
                />
              </Col>
            </Row>
          </Checkbox.Group>
        </Space>
      )}
    </>
  );
}

export default String;
