import * as actionTypes from "./actionTypes";

import {
  ICollection,
  CollectionsState,
  CollectionAction,
} from "../types/global";

const initialState: CollectionsState = {
  collections: [],
  selectedCollection: undefined,
};

const reducer = (
  state: CollectionsState = initialState,
  action: CollectionAction
): CollectionsState => {
  switch (action.type) {
    case actionTypes.INIT_COLLECTION:
      return {
        ...state,
        collections: action.collections || [],
      };
    case actionTypes.ADD_COLLECTION:
      return {
        ...state,
        collections: state.collections
          .concat(action.collection)
          .sort((a, b) => a.name.localeCompare(b.name)),
      };

    case actionTypes.REMOVE_COLLECTION:
      const updatedCollections: ICollection[] = state.collections
        .filter((article) => article.id !== action.collection.id)
        .sort((a, b) => a.name.localeCompare(b.name));
      return {
        ...state,
        collections: updatedCollections,
        selectedCollection: undefined,
      };

    case actionTypes.SET_COLLECTION:
      const selectedCollection: ICollection = action.collection;

      return {
        ...state,
        selectedCollection,
      };

    case actionTypes.UPDATE_COLLECTION:
      const updatedCollection: ICollection = action.collection;

      state.collections = state.collections.map((collection) => {
        if (collection.id === updatedCollection.id) {
          return updatedCollection;
        }
        return collection;
      });

      state.selectedCollection = updatedCollection;

      return { ...state };
  }

  return state;
};

export default reducer;
