import { Radio, Space, Input, Switch } from "antd";
import React, { useEffect } from "react";
import {
  ICollectionSchema,
  ERelationShip,
  CollectionsState,
  ICollection,
} from "../../types/global";
import { useSelector, shallowEqual } from "react-redux";

import RequiredText from "../common/RequiredText";

function Ref({
  state,
  setState,
}: {
  state: ICollectionSchema;
  setState: Function;
}) {
  const updateState = (key: string, value: any) => {
    const updatedData: any = {};
    updatedData[key] = value;
    setState((prev: ICollectionSchema) => ({ ...prev, ...updatedData }));
  };

  const collections: ICollection[] = useSelector(
    (state: CollectionsState) => state.collections,
    shallowEqual
  );

  return (
    <>
      {state.type === "ref" && (
        <Space direction="vertical" style={{ width: "100%", overflow: "auto" }}>
          <span>Name for the type</span>
          <Input
            placeholder="Type Name"
            value={state.name}
            onChange={(e) => updateState("name", e.target.value)}
          />
          <RequiredText />
          <span>Relationship</span>
          <Radio.Group
            value={state.relationship}
            onChange={(e) => updateState("relationship", e.target.value)}
          >
            <Radio.Button value={ERelationShip.hasMany}>Has Many</Radio.Button>
            <Radio.Button value={ERelationShip.hasOne}>Has One</Radio.Button>
          </Radio.Group>

          <span>Auto Populate</span>

          <Radio.Group
            onChange={(e) => updateState("autopopulate", e.target.value)}
            value={state.autopopulate}
          >
            <Radio value={true}>True</Radio>
            <Radio value={false}>False</Radio>
          </Radio.Group>

          <span>Required</span>
          <Switch
            checkedChildren="true"
            unCheckedChildren="false"
            defaultChecked={state.required}
            onChange={(e) => updateState("required", e)}
          />

          <span>Ref</span>

          <Radio.Group
            value={state.ref}
            onChange={(e) => updateState("ref", e.target.value)}
            style={{ height: "300px", overflow: "auto" }}
          >
            {collections.map(({ name }, index) => (
              <Radio.Button
                value={name}
                style={{ textTransform: "capitalize" }}
                key={index}
              >
                {name}
              </Radio.Button>
            ))}
          </Radio.Group>
        </Space>
      )}
    </>
  );
}

export default Ref;
