import {
  Button,
  Input,
  Space,
  Form,
  Row,
  Col,
  Select,
  InputNumber,
  DatePicker,
  Badge,
} from "antd";
import Highlighter from "react-highlight-words";
import { CaretRightOutlined, FilterOutlined } from "@ant-design/icons";
import queryString from "query-string";
import { useHistory, useParams } from "react-router";
import { useEffect, useState } from "react";
import { ICollection, ICollectionSchema } from "../../types/global";
import moment from "moment";
import RefSelect from "./RefSelect";

const { Option } = Select;

const getKeyValue =
  <U extends keyof T, T extends object>(key: U) =>
  (obj: T) =>
    obj[key];

function TableFilter({
  selectedCollection,
}: {
  selectedCollection: ICollection;
}) {
  const { collection: collectionName }: any = useParams();
  const history = useHistory();
  const queryParams: any = queryString.parse(history.location.search);
  const [collapsed, setCollapsed] = useState(false);

  const schema = selectedCollection?.schema;

  const handleSearch = (values: any) => {
    let query: any = queryString.parse(history.location.search);
    Object.keys(values).forEach((key) => {
      if (values[key] && values[key].length > 0) {
        query[`filter[${key}][$regex]`] = `.*${values[key]}.*`;
      }
    });
    handleFilterChange(query);
  };

  const handleFilterChange = (query) => {
    query = decodeURI(queryString.stringify(query));
    history.replace(`/data/${collectionName}?${query}`);
  };

  const clearFilter = () => {
    let query: any = queryString.parse(history.location.search);
    let resetQuery = {
      action: query["action"],
      "page[limit]": query["page[limit]"],
      "page[number]": query["page[number]"],
    };
    handleFilterChange(resetQuery);
    setCollapsed(false);
  };

  return (
    <>
      <Badge count={Object.keys(queryParams).length - 3}>
        <Button
          onClick={() => {
            setCollapsed(!collapsed);
          }}
        >
          Filter <FilterOutlined />
        </Button>
      </Badge>
      {collapsed && schema && (
        <div
          style={{
            width: "100vw",
            height: "100vh",
            backgroundColor: "transparent",
            position: "fixed",
            top: 0,
            left: 0,
            zIndex: 10,
          }}
        >
          <div
            style={{
              top: 0,
              right: 0,
              position: "fixed",
              width: 350,
              backgroundColor: "white",
              height: "100vh",
              padding: 20,
              boxShadow: "-28px 11px 75px -33px rgba(0,0,0,0.74)",
            }}
          >
            <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
              <Col
                className="gutter-row"
                span={4}
                style={{ display: "flex", alignItems: "center" }}
              >
                <Button
                  shape="circle"
                  icon={<CaretRightOutlined />}
                  onClick={() => setCollapsed(false)}
                  type="primary"
                />
              </Col>
              <Col className="gutter-row" span={6}>
                <span style={{ fontSize: 20 }}>Filters</span>
              </Col>
            </Row>
            <Row>
              <Col>
                <Form
                  name="basic"
                  onFinish={handleSearch}
                  onFinishFailed={() => {}}
                  layout="vertical"
                  size="large"
                  style={{
                    overflowY: "auto",
                    height: "100vh",
                  }}
                >
                  {schema &&
                    Object.keys(schema).map((key: any) => {
                      const schemaValue = getKeyValue<
                        keyof ICollectionSchema,
                        ICollectionSchema
                      >(key)(schema);
                      let initial;
                      switch (schemaValue.type) {
                        // case "ref":
                        //   initial = queryParams[
                        //     `filter[${schemaValue.name}][$regex]`
                        //   ]
                        //     ? queryParams[
                        //         `filter[${schemaValue.name}][$regex]`
                        //       ].replaceAll(".*", "")
                        //     : "";
                        //   return (
                        //     <RefSelect
                        //       schemaValue={schemaValue}
                        //       action={"create"}
                        //       selectedRecord={selectedCollection}
                        //     />
                        //   );

                        case "string":
                          initial = queryParams[
                            `filter[${schemaValue.name}][$regex]`
                          ]
                            ? queryParams[
                                `filter[${schemaValue.name}][$regex]`
                              ].replaceAll(".*", "")
                            : "";
                          return (
                            <Form.Item
                              label={
                                <span
                                  style={{
                                    textTransform: "capitalize",
                                    color: "#434343",
                                  }}
                                >
                                  {schemaValue.name}
                                </span>
                              }
                              name={schemaValue.name}
                              initialValue={initial}
                            >
                              {schemaValue.enum.length ? (
                                <Select
                                  placeholder={`Select ${schemaValue.name}`}
                                >
                                  <>
                                    {schemaValue.enum.map((e: any) => (
                                      <Option value={e}>{e}</Option>
                                    ))}
                                  </>
                                </Select>
                              ) : (
                                <Input />
                              )}
                            </Form.Item>
                          );

                        case "number":
                          initial = queryParams[
                            `filter[${schemaValue.name}][$regex]`
                          ]
                            ? queryParams[
                                `filter[${schemaValue.name}][$regex]`
                              ].replaceAll(".*", "")
                            : "";
                          return (
                            <Form.Item
                              label={
                                <span
                                  style={{
                                    textTransform: "capitalize",
                                    color: "#434343",
                                  }}
                                >
                                  {schemaValue.name}
                                </span>
                              }
                              name={schemaValue.name}
                              initialValue={initial}
                            >
                              <InputNumber style={{ width: 300 }} />
                            </Form.Item>
                          );
                        case "date":
                          initial = queryParams[
                            `filter[${schemaValue.name}][$regex]`
                          ]
                            ? queryParams[
                                `filter[${schemaValue.name}][$regex]`
                              ].replaceAll(".*", "")
                            : null;
                          return (
                            <Form.Item
                              label={
                                <span
                                  style={{
                                    textTransform: "capitalize",
                                    color: "#434343",
                                  }}
                                >
                                  {schemaValue.name}
                                </span>
                              }
                              name={schemaValue.name}
                              initialValue={initial && moment(initial)}
                            >
                              <DatePicker />
                            </Form.Item>
                          );
                        case "boolean":
                          initial = queryParams[
                            `filter[${schemaValue.name}][$regex]`
                          ]
                            ? queryParams[
                                `filter[${schemaValue.name}][$regex]`
                              ].replaceAll(".*", "")
                            : null;
                          return (
                            <Form.Item
                              label={
                                <span
                                  style={{
                                    textTransform: "capitalize",
                                    color: "#434343",
                                  }}
                                >
                                  {schemaValue.name}
                                </span>
                              }
                              name={schemaValue.name}
                              initialValue={initial}
                            >
                              <Select placeholder={`Select`}>
                                <Option value={"true"}>True</Option>
                                <Option value={"false"}>False</Option>
                              </Select>
                            </Form.Item>
                          );

                        default:
                          return null;
                      }
                    })}

                  <Form.Item
                    style={{
                      position: "fixed",
                      bottom: 10,
                      display: "flex",
                      width: "100%",
                    }}
                  >
                    <Space>
                      <Button type="primary" htmlType="submit">
                        Search
                      </Button>
                      <Button onClick={clearFilter}>Clear</Button>
                    </Space>
                  </Form.Item>
                </Form>
              </Col>
            </Row>
          </div>
        </div>
      )}
    </>
  );
}

export { TableFilter };
