import { Modal } from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import { ICollection } from "../types/global";

const { confirm } = Modal;

function RemoveCollectionModal({
  removeCollectionType,
  collection,
  key,
}: {
  removeCollectionType: Function;
  collection: ICollection;
  key: any;
}) {
  const deleteType = () => {
    const { schema } = collection;
    delete schema[key];
    collection.schema = schema;
    collection.fields = collection.fields - 1;
    removeCollectionType(collection);
  };

  return confirm({
    title: "Sure you want to delete type?",
    icon: <ExclamationCircleOutlined />,
    content: null,
    onOk() {
      deleteType();
    },
    onCancel() {
      console.log("Cancel");
    },
  });
}

export default RemoveCollectionModal;
