import React from "react";
import { PageHeader, Checkbox, Space, message, Row, Col, Tag } from "antd";
import { useDispatch, useSelector } from "react-redux";
import { CollectionsState, ICollection } from "../types/global";
import { Dispatch } from "redux";
import { updateCollection } from "../store/actionCreators";

const key = "updatable";

function AuthPage() {
  const dispatch: Dispatch<any> = useDispatch();

  const collections: ICollection[] = useSelector(
    (state: CollectionsState) => state.collections
  ).filter((collection) => collection.show);

  const updateCollectionAction = React.useCallback(
    (collection: ICollection) => dispatch(updateCollection(collection)),
    [dispatch]
  );

  function onChange(collection: ICollection, checkedValues) {
    message.loading({ content: "Saving...", key });

    const allMethods = {
      list: false,
      create: false,
      read: false,
      update: false,
      delete: false,
      deleteAll: false,
    };
    checkedValues.forEach((value) => {
      allMethods[value] = true;
    });

    collection.publicMethods = allMethods;

    updateCollectionAction(collection);
  }

  const parseOptions = (collection: ICollection) => {
    return Object.keys(collection.publicMethods || {}).map((method) => ({
      label: method,
      value: `${method}`,
    }));
  };

  const parseDefaults = (collection: ICollection) => {
    return Object.keys(collection.publicMethods || {})
      .filter((method) => collection.publicMethods[method])
      .map((method) => `${method}`);
  };

  const style = { background: "#0092ff", padding: "8px 0" };

  return (
    <div>
      <PageHeader
        title="Auth Management"
        subTitle="Public routes available"
        ghost={false}
      />

      <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
        {collections.map((collection: ICollection) => {
          return (
            <Col className="gutter-row" span={12}>
              <PageHeader
                title={<Tag>{collection.name}</Tag>}
                style={{ textTransform: "capitalize" }}
              />
              <PageHeader>
                <Checkbox.Group
                  options={parseOptions(collection)}
                  defaultValue={parseDefaults(collection)}
                  onChange={(checkedValues) =>
                    onChange(collection, checkedValues)
                  }
                />
              </PageHeader>
            </Col>
          );
        })}
      </Row>
      <br />
      <br />
    </div>
  );
}

export default AuthPage;
