import { Space, Switch, Input } from "antd";
import React from "react";
import { ICollectionSchema } from "../../types/global";

function Boolean({
  state,
  setState,
}: {
  state: ICollectionSchema & { name: string };
  setState: Function;
}) {
  const updateState = (key: string, value: any) => {
    const updatedData: any = {};
    updatedData[key] = value;
    setState((prev: ICollectionSchema) => ({ ...prev, ...updatedData }));
  };
  return (
    <>
      {state.type === "boolean" && (
        <Space direction="vertical" style={{ width: "100%" }}>
          <span>Name for the type</span>
          <Input
            placeholder="Type Name"
            onChange={(e) => updateState("name", e.target.value)}
            defaultValue={state.name}
          />
          <span>Default value</span>
          <Switch
            checkedChildren="true"
            unCheckedChildren="false"
            defaultChecked={state.default}
            onChange={(e) => updateState("default", e)}
          />
          <span>Required</span>
          <Switch
            checkedChildren="true"
            unCheckedChildren="false"
            defaultChecked={state.required}
            onChange={(e) => updateState("required", e)}
          />
        </Space>
      )}
    </>
  );
}

export default Boolean;
