import { Dispatch, useCallback, useEffect, useRef, useState } from "react";
import {
  Table,
  PageHeader,
  Button,
  Tag,
  Modal,
  message,
  Breadcrumb,
  Image,
  Space,
  Menu,
  Dropdown,
  BackTop,
} from "antd";
import { ICurdBody, crudCollection, EOperations } from "../http";
import { useParams, useHistory, Link } from "react-router-dom";
import { PlusOutlined, FilterOutlined } from "@ant-design/icons";
import { shallowEqual, useSelector } from "react-redux";
import { CollectionsState } from "../types/global";
import queryString from "query-string";
import CreateCollectionAction from "../components/DataFlow/CreateCollectionAction";
import ShowCollectionAction from "../components/DataFlow/ShowCollectionAction";
import {
  EditOutlined,
  DeleteOutlined,
  ExclamationCircleOutlined,
  EyeOutlined,
  MoreOutlined,
  MenuOutlined,
} from "@ant-design/icons";

import moment from "moment";
import prettySize from "prettysize";
import UpdateCollectionAction from "../components/DataFlow/UpdateCollectionAction";
import { TableFilter } from "../components/DataFlow/TableFilter";
import useBreadcrumbs from "use-react-router-breadcrumbs";

const { Column } = Table;
const { confirm } = Modal;
interface IQueryParams {
  action?: string;
  id?: string;
}

function DataFlowDetailPage() {
  const { collection: collectionName }: any = useParams();
  const history = useHistory();
  const queryParams: IQueryParams = queryString.parse(history.location.search);
  const query = history.location.search;
  const [list, setList] = useState<Array<{ name: string; type: string }>>([]);
  const [columns, setColumns] = useState([]);
  const breadcrumbs = useBreadcrumbs();

  const [loading, setLoading] = useState(true);
  const [rowSelection, setRowSelection] = useState<any>([]);
  const [pagination, setPagination] = useState<{
    current: number;
    pageSize: number;
    total?: number;
  }>({
    current: 1,
    pageSize: 10,
  });

  const selectedCollection = useSelector(
    (state: CollectionsState) => state.collections,
    shallowEqual
  )
    .filter((collection) => collection.show)
    .find((c) => c.name === collectionName);

  const getCollectionList = async (model: string) => {
    setLoading(true);
    try {
      const action: ICurdBody = {
        model: model,
        operations: EOperations.list,
        data: {},
      };
      const { data } = await crudCollection(action, query);
      setPagination(data.pagination);
      setList(data.list);
      setColumns(data.column);
    } catch (error) {
      console.log(error);
    }
    setLoading(false);
  };

  const deleteRecord = async (id: string) => {
    setLoading(true);
    try {
      const action: ICurdBody = {
        model: collectionName,
        operations: EOperations.delete,
        data: {
          id: id,
        },
      };
      await crudCollection(action);
      message.success("Record Deleted");
      getCollectionList(collectionName);
    } catch (error) {
      console.log(error);
    }
    setLoading(false);
  };

  const deleteManyRecord = async () => {
    setLoading(true);
    try {
      const action: ICurdBody = {
        model: collectionName,
        operations: EOperations.deleteAll,
        data: {
          id: rowSelection,
        },
      };
      await crudCollection(action);
      message.success("Records Deleted");
      setRowSelection([]);
      getCollectionList(collectionName);
    } catch (error) {
      console.log(error);
    }
    setLoading(false);
  };

  useEffect(() => {
    if (selectedCollection && queryParams["action"] === "list") {
      setColumns([]);
      setList([]);
      getCollectionList(collectionName);
    }
  }, [collectionName, selectedCollection, window.location.href]);

  const dataSource = list
    ? list.map((l: any, index) => ({ key: index, ...l }))
    : [];

  const dataColumns: any = columns
    ? columns.map(
        ({
          name,
          type,
          file,
        }: {
          name: string;
          type: string;
          file: boolean;
        }) => ({
          title: name.toString(),
          dataIndex: name.toString(),
          key: name.toString(),
          type,
          file,
        })
      )
    : [];

  dataColumns.push({
    title: "Action",
    key: "operation",
    fixed: "right",
    width: 20,
    type: "operation",
  });

  const handleTableChange = (pagination: any, filters: any, sorter: any) => {
    let query: any = queryString.parse(history.location.search);
    query["page[number]"] = pagination.current;
    query["page[limit]"] = pagination.pageSize;
    query = decodeURI(queryString.stringify(query));
    history.replace(`/data/${selectedCollection.name}?${query}`);
  };

  if (
    queryParams.action &&
    queryParams.action === "create" &&
    selectedCollection
  )
    return <CreateCollectionAction />;
  if (
    queryParams.action &&
    queryParams.action === "update" &&
    selectedCollection
  )
    return <UpdateCollectionAction />;

  if (queryParams.action && queryParams.action === "show" && selectedCollection)
    return <ShowCollectionAction />;

  return (
    <>
      <BackTop />

      <PageHeader
        style={{ marginTop: 2 }}
        ghost={false}
        onBack={() => window.history.back()}
        title={
          <Space>
            <span style={{ textTransform: "capitalize" }}>
              {collectionName}{" "}
            </span>
            {pagination?.total > 0 && (
              <Tag color="geekblue">{pagination.total}</Tag>
            )}
            <Breadcrumb>
              {breadcrumbs.map(({ match, breadcrumb }) => (
                <Breadcrumb.Item>
                  <Link to={match.url}>{breadcrumb}</Link>
                </Breadcrumb.Item>
              ))}
            </Breadcrumb>
          </Space>
        }
        extra={[
          rowSelection.length ? (
            <Button
              onClick={() => {
                return confirm({
                  title: "Sure you want to delete selected records?",
                  icon: <ExclamationCircleOutlined />,
                  content: null,
                  onOk() {
                    deleteManyRecord();
                  },
                  onCancel() {
                    console.log("Cancel");
                  },
                });
              }}
            >
              <DeleteOutlined />
              Delete
            </Button>
          ) : null,
          <TableFilter selectedCollection={selectedCollection} />,
          <Button
            key="1"
            type="primary"
            onClick={() => {
              history.push(`/data/${collectionName}?action=create`);
            }}
          >
            <PlusOutlined />
            Create
          </Button>,
        ]}
      ></PageHeader>
      <Table
        loading={loading}
        style={{
          padding: 10,
        }}
        scroll={{ x: "100vw" }}
        dataSource={dataSource}
        size="middle"
        rowKey={(record) => record._id}
        rowSelection={{
          selectedRowKeys: rowSelection,
          onChange: (selectedRows) => {
            setRowSelection(selectedRows);
          },
        }}
        pagination={pagination}
        onChange={handleTableChange}
      >
        {dataColumns.map((column: any) => {
          switch (column.type) {
            case "objectId":
              return (
                <Column
                  title={
                    <span style={{ textTransform: "capitalize" }}>ID</span>
                  }
                  dataIndex={column.dataIndex}
                  key={column.key}
                  fixed="left"
                  width={220}
                />
              );
            case "boolean":
              return (
                <Column
                  title={
                    <span style={{ textTransform: "capitalize" }}>
                      {column.title}
                    </span>
                  }
                  dataIndex={column.dataIndex}
                  key={column.key}
                  width={200}
                  render={(text, record, index) => {
                    if (text === undefined) return null;
                    return (
                      <>
                        {text ? (
                          <Tag
                            color={"green"}
                            style={{ textTransform: "capitalize" }}
                          >
                            {text.toString()}
                          </Tag>
                        ) : (
                          <Tag
                            color={"red"}
                            style={{ textTransform: "capitalize" }}
                          >
                            {text.toString()}
                          </Tag>
                        )}
                      </>
                    );
                  }}
                />
              );

            case "date":
              return (
                <Column
                  title={
                    <span style={{ textTransform: "capitalize" }}>
                      {column.title}
                    </span>
                  }
                  dataIndex={column.dataIndex}
                  key={column.key}
                  width={200}
                  render={(text: Date) => {
                    return text ? (
                      <span>{moment(text).format("MM/DD/YYYY hh:mm")}</span>
                    ) : null;
                  }}
                />
              );

            case "mixed":
              return (
                <Column
                  title={
                    <span
                      style={{
                        textTransform: "capitalize",
                        display: "flex",
                        justifyContent: "space-between",
                      }}
                    >
                      {column.title}

                      <Tag color="magenta">JSON</Tag>
                    </span>
                  }
                  width={200}
                  dataIndex={column.dataIndex}
                  key={column.key}
                  render={(text: any) => {
                    return text ? (
                      <span>
                        {Object.keys(text).map((key) => (
                          <Tag>{key}</Tag>
                        ))}
                      </span>
                    ) : null;
                  }}
                />
              );
            case "operation":
              return (
                <Column
                  title={
                    <Space>
                      <MenuOutlined />
                    </Space>
                  }
                  width={30}
                  dataIndex={column.dataIndex}
                  key={column.key}
                  fixed="right"
                  render={(text, record: any) => {
                    return (
                      <Dropdown
                        overlay={
                          <Menu>
                            <Menu.Item>
                              <Button
                                style={{ width: "100%" }}
                                className="button-blue"
                                icon={<EyeOutlined />}
                                onClick={() => {
                                  history.push(
                                    `/data/${collectionName}?action=show&id=${record._id}`
                                  );
                                }}
                              >
                                View
                              </Button>
                            </Menu.Item>
                            <Menu.Item>
                              <Button
                                style={{ width: "100%" }}
                                className="button-green"
                                icon={<EditOutlined />}
                                onClick={() => {
                                  history.push(
                                    `/data/${collectionName}?action=update&id=${record._id}`
                                  );
                                }}
                              >
                                Edit
                              </Button>
                            </Menu.Item>
                            <Menu.Item>
                              <Button
                                icon={<DeleteOutlined />}
                                className="button-red"
                                onClick={() => {
                                  return confirm({
                                    title:
                                      "Sure you want to delete this record?",
                                    icon: <ExclamationCircleOutlined />,
                                    content: null,
                                    onOk() {
                                      deleteRecord(record._id);
                                    },
                                    onCancel() {
                                      console.log("Cancel");
                                    },
                                  });
                                }}
                              >
                                Delete
                              </Button>
                            </Menu.Item>
                          </Menu>
                        }
                        placement="bottomLeft"
                      >
                        <MoreOutlined
                          style={{ cursor: "pointer", maxHeight: 10 }}
                        />
                      </Dropdown>
                    );
                  }}
                />
              );
            case "hasone":
              return (
                <Column
                  title={
                    <span
                      style={{
                        textTransform: "capitalize",
                        display: "flex",
                        justifyContent: "space-between",
                      }}
                    >
                      {column.title}

                      {column.file && <Tag color="cyan">File</Tag>}
                    </span>
                  }
                  dataIndex={column.dataIndex}
                  key={column.key}
                  width={200}
                  render={(text) => {
                    return (
                      text && (
                        <Space direction="vertical">
                          {column.file ? (
                            <Space>
                              <Image
                                preview={false}
                                width={100}
                                height={100}
                                src={"/admin/file/get/" + text.filename}
                                fallback={"/img/file.svg"}
                              />

                              <span
                                style={{
                                  color: "GrayText",
                                  display: "flex",
                                  justifyContent: "space-between",
                                  flexDirection: "column",
                                }}
                              >
                                <b>{text.originalname}</b>
                                {prettySize(text.size)} {text.mimetype}
                              </span>
                            </Space>
                          ) : typeof text === "string" ? (
                            <Link
                              to={`/data/${column.key}?action=show&id=${text}`}
                            >
                              <span>{text} </span>
                            </Link>
                          ) : (
                            Object.values(text).map((value, index) => {
                              if (!(typeof value === "string")) return null;
                              if (index === 1) return <span>{value} </span>;
                              return null;
                            })
                          )}
                        </Space>
                      )
                    );
                  }}
                />
              );
            case "hasmany":
              return (
                <Column
                  title={
                    <span
                      style={{
                        textTransform: "capitalize",
                        display: "flex",
                        justifyContent: "space-around",
                      }}
                    >
                      {column.title}

                      {column.file && <Tag color="cyan">Files</Tag>}
                    </span>
                  }
                  width={200}
                  dataIndex={column.dataIndex}
                  key={column.key}
                  render={(text) => {
                    return (
                      <span>
                        {text?.length} {column.title}
                      </span>
                    );
                  }}
                ></Column>
              );
            default:
              return (
                <Column
                  title={
                    <span style={{ textTransform: "capitalize" }}>
                      {column.title}
                    </span>
                  }
                  width={200}
                  dataIndex={column.dataIndex}
                  key={column.key}
                  render={(text = "") => {
                    return (
                      <>
                        {selectedCollection.schema[column.key].enum.length >
                        0 ? (
                          <Tag
                            color="geekblue"
                            style={{ textTransform: "capitalize" }}
                          >
                            {text?.toString()}
                          </Tag>
                        ) : (
                          <span>
                            {text?.toString().slice(0, 80)}
                            {text?.toString().length > 80 ? "..." : null}
                          </span>
                        )}
                      </>
                    );
                  }}
                />
              );
          }
        })}
      </Table>
    </>
  );
}

export default DataFlowDetailPage;
