export enum ESchemaTypes {
  string = "string",
  number = "number",
  boolean = "boolean",
  ref = "ref",
  date = "date",
  array = "array",
  object = "object",
  enums = "enums",
  file = "file",
  json = "json",
}

export enum ERelationShip {
  hasOne = "hasone",
  hasMany = "hasmany",
  none = "none",
}

export interface ICollectionSchema {
  name: string;
  type: ESchemaTypes;
  default?: any;
  required?: boolean;
  index?: boolean;
  unique?: boolean;
  lowercase?: boolean;
  uppercase?: boolean;
  trim?: boolean;
  minLength?: number;
  maxLength?: number;
  ref?: string;
  relationship?: ERelationShip;
  enum: Array<string>;
  autopopulate: boolean;
}

export interface ICrudOperations {
  list: boolean;
  create: boolean;
  read: boolean;
  update: boolean;
  delete: boolean;
  deleteAll: boolean;
}
export interface IPublicMethods {
  list: boolean;
  create: boolean;
  read: boolean;
  update: boolean;
  delete: boolean;
  deleteAll: boolean;
}

export interface ICollection {
  id: string;
  name: string;
  schema: any;
  fields: number;
  show: boolean;
  operations: ICrudOperations;
  publicMethods?: IPublicMethods;
  x: Number;
  y: Number;
}

export type CollectionsState = {
  collections: ICollection[];
  selectedCollection: ICollection | undefined;
  newCollection?: ICollection;
};

export type CollectionAction = {
  type: string;
  collection: ICollection;
  collections?: Array<ICollection>;
};

export type DispatchType = (args: CollectionAction) => CollectionAction;
