import React, { useState, useEffect } from "react";
import { PageHeader, Row, Col, Tree, Button } from "antd";
import { useSelector } from "react-redux";
import { CarryOutOutlined } from "@ant-design/icons";
import {
    ICollection,
    CollectionsState,
} from "../types/global";


function ApiDocsPage() {
    const [selectedCollection, setSelectedCollection] = useState({
        collection: "",
        method: "",
    });


    const collections: ICollection[] = useSelector(
        (state: CollectionsState) => state.collections
    );

    const onSelect = (selectedKeys: React.Key[], info: any) => {
        const [collection, method] = selectedKeys[0].toString().split("-");
        if (method) {
            setSelectedCollection({
                collection,
                method,
            });
        } else {
            setSelectedCollection({
                collection,
                method: data.find((model) => model.title === collection).children[0]
                    .title,
            });
        }
    };

    const data = collections
        .filter((collection: ICollection) => collection.show)
        .map((collection: ICollection) => ({
            title: collection.name,
            key: collection.name,
            icon: <CarryOutOutlined />,
            children: [
                {
                    title: "list",
                    key: `${collection.name}-list`,
                    icon: <CarryOutOutlined />,
                },

                {
                    title: "create",
                    key: `${collection.name}-create`,
                    icon: <CarryOutOutlined />,
                },
                {
                    title: "read",
                    key: `${collection.name}-read`,
                    icon: <CarryOutOutlined />,
                },
                {
                    title: "update",
                    key: `${collection.name}-update`,
                    icon: <CarryOutOutlined />,
                },
                {
                    title: "delete",
                    key: `${collection.name}-delete`,
                    icon: <CarryOutOutlined />,
                },
                {
                    title: "deleteAll",
                    key: `${collection.name}-deleteAll`,
                    icon: <CarryOutOutlined />,
                },
            ].filter((method: any) => collection.operations[method.title]),
        }))
        .filter((d) => d.children.length);



    useEffect(() => {
        if (data.length) {
            setSelectedCollection({
                collection: data[0].title,
                method: data[0].children[0].title,
            });
        }
    }, []);


    return (
        <div>
            <PageHeader
                title="Api Documentation"
                subTitle="Powered by swagger.io"
                ghost={false}
                extra={[
                    <Button
                        className="button-blue"
                        onClick={() => {
                            window
                                .open("http://localhost:3002/admin/api-docs/", "_blank")
                                .focus();
                        }}
                    >
                        Open docs
                    </Button>,
                ]}
            />

            <Row>
                <Col lg={3} style={{ height: "100vh" }}>
                    <Tree
                        showLine={true}
                        showIcon={false}
                        defaultExpandParent={true}
                        onSelect={onSelect}
                        treeData={data}
                        style={{
                            backgroundColor: "transparent",
                            padding: 20,
                            height: "100vh",
                        }}
                    />
                </Col>
                <Col lg={21} style={{ height: "100vh" }}>
                    <PageHeader
                        title={
                            <span style={{ textTransform: "capitalize" }}>
                                {selectedCollection?.collection}
                            </span>
                        }
                        subTitle={`${selectedCollection.method}`}
                    ></PageHeader>
                    <Row>

                    </Row>
                </Col>
            </Row>
        </div>
    );
}

export default ApiDocsPage;
