import { Form } from "antd";
import JSONInput from "react-json-editor-ajrm";
import locale from "react-json-editor-ajrm/locale/en";
import { useState } from "react";


function JSONEditor({ schemaValue, form, action, selectedRecord }: { schemaValue: any; form: any, action: string, selectedRecord: any }) {
    const [jsonData, setJsonData] = useState(
        action === "update" && selectedRecord[schemaValue.name]
            ? selectedRecord[schemaValue.name]
            : null);

    return (
        <Form.Item
            label={
                <span
                    style={{
                        textTransform: "capitalize",
                        color: "#434343",
                    }}
                >
                    {schemaValue.name}
                </span>
            }
            name={schemaValue.name}
            rules={[
                {
                    required: schemaValue.required,
                },
            ]}
        >
            <div style={{ border: "1px solid #161f59", borderRadius: 1 }}>

                <JSONInput

                    theme="dark_mitsuketa_tribute"

                    locale={locale}
                    placeholder={jsonData}
                    onChange={(e) => {
                        form.setFieldsValue({
                            [schemaValue.name]: { ...e.jsObject },
                        })
                        setJsonData(e.jsObject);
                    }}
                    height="550px"
                    width="100%"
                />
            </div>
        </Form.Item>
    );
}

export default JSONEditor;
