import { useEffect, useState } from "react";
import {
  PageHeader,
  Button,
  Tag,
  Space,
  Modal,
  message,
  Skeleton,
  Image,
  Pagination,
  Upload,
  Checkbox,
} from "antd";
import { ICurdBody, crudCollection, EOperations } from "../../http";
import { PlusOutlined, FilterOutlined } from "@ant-design/icons";

import {
  DeleteOutlined,
  ExclamationCircleOutlined,
  InboxOutlined,
} from "@ant-design/icons";

import prettySize from "prettysize";

import packageJson from "../../../package.json";

const { confirm } = Modal;
const { Dragger } = Upload;

function FilesView({
  setCustomFiles,
  deleteButton,
  filterButton,
  selectedFiles: defaultSelectedFiles = [],
}: {
  setCustomFiles?: Function;
  deleteButton?: boolean;
  filterButton?: boolean;
  selectedFiles: Array<string>;
}) {
  const collectionName = "file";

  const [list, setList] = useState<Array<any>>([]);

  const [selectedFiles, setSelectedFiles] = useState(defaultSelectedFiles);

  const [loading, setLoading] = useState(true);
  const [uploadModal, setUploadModal] = useState(false);

  const [pagination, setPagination] = useState<{
    current: number;
    pageSize: number;
    total?: number;
  }>({
    current: 1,
    pageSize: 10,
  });

  useEffect(() => {
    if (typeof setCustomFiles === "function") {
      setCustomFiles([...selectedFiles]);
    }
  }, [selectedFiles]);

  const props = {
    multiple: true,
    name: "manual",
    action: "/admin/file/manual",
    defaultFileList: [],
    onChange(info) {
      const { status } = info.file;
      if (status !== "uploading") {
      }
      if (status === "done") {
        message.success(`${info.file.name} file uploaded successfully.`);
      } else if (status === "error") {
        message.error(`${info.file.name} file upload failed.`);
      }

      info.fileList = [];
    },
    onDrop(e) {
      console.log("Dropped files", e.dataTransfer.files);
    },
  };

  const getCollectionList = async (model: string, pagination: any) => {
    setLoading(true);
    try {
      const action: ICurdBody = {
        model: model,
        operations: EOperations.list,
        data: {
          query: {
            pagination: pagination,
          },
        },
      };
      const { data } = await crudCollection(action);
      setPagination(data.pagination);
      setList(data.list);
    } catch (error) {
      console.log(error);
    }
    setLoading(false);
  };

  const deleteManyRecord = async () => {
    setLoading(true);
    try {
      const action: ICurdBody = {
        model: collectionName,
        operations: EOperations.deleteAll,
        data: {
          id: selectedFiles,
        },
      };
      await crudCollection(action);
      message.success("Records Deleted");
      setSelectedFiles([]);
      getCollectionList(collectionName, pagination);
    } catch (error) {
      console.log(error);
    }
    setLoading(false);
  };

  useEffect(() => {
    getCollectionList(collectionName, pagination);
  }, []);

  return (
    <>
      {loading ? (
        <div style={{ padding: 20 }}>
          <Skeleton active={true} />
        </div>
      ) : (
        <>
          <PageHeader
            style={{ marginTop: 2 }}
            ghost={true}
            title={
              <Space>
                {pagination?.total > 0 && (
                  <Tag color="green">Total {pagination.total}</Tag>
                )}

                <Tag color="blue">Selected {selectedFiles.length}</Tag>
              </Space>
            }
            extra={[
              selectedFiles.length && deleteButton ? (
                <Button
                  onClick={() => {
                    return confirm({
                      title: "Sure you want to delete selected records?",
                      icon: <ExclamationCircleOutlined />,
                      content: null,
                      onOk() {
                        deleteManyRecord();
                      },
                      onCancel() {
                        console.log("Cancel");
                      },
                    });
                  }}
                >
                  <DeleteOutlined />
                  Delete
                </Button>
              ) : null,
              filterButton && (
                <Button key="2">
                  <FilterOutlined />
                  Filter
                </Button>
              ),
              <Button
                key="1"
                type="primary"
                onClick={() => {
                  setUploadModal(true);
                }}
              >
                <PlusOutlined />
                Upload
              </Button>,
            ]}
          />

          <div className="grid-responsive">
            {list.map((l) => (
              <div
                key={l._id}
                className="on-hover"
                style={{
                  border: selectedFiles.includes(l._id)
                    ? "1px solid #40a9ff"
                    : "none",
                  backgroundColor: "white",
                }}
              >
                <Checkbox
                  style={{ padding: 10, position: "absolute", zIndex: 99 }}
                  checked={selectedFiles.includes(l._id)}
                  onChange={() => {
                    setSelectedFiles((prev: any) => {
                      let tempArray = [...prev];
                      if (tempArray.includes(l._id)) {
                        tempArray = tempArray.filter(
                          (fileId) => fileId !== l._id
                        );
                      } else {
                        tempArray.push(l._id);
                      }
                      return tempArray;
                    });
                  }}
                ></Checkbox>
                <div style={{ display: "flex", justifyContent: "center" }}>
                  <Image
                    preview={false}
                    width={200}
                    height={200}
                    src={"/admin/file/get/" + l.filename}
                    fallback={"/img/file.svg"}
                  />
                </div>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    padding: 10,
                  }}
                >
                  <b>{l.originalname}</b>
                  <span
                    style={{
                      color: "GrayText",
                      display: "flex",
                      justifyContent: "space-between",
                    }}
                  >
                    {prettySize(l.size)} {l.mimetype}
                  </span>
                </div>
                <Button
                  style={{ float: "right", margin: 5 }}
                  onClick={() => {
                    window
                      .open(packageJson.proxy + "/admin/file/get/" + l.filename)
                      .focus();
                  }}
                >
                  Open
                </Button>
              </div>
            ))}
          </div>

          <Pagination
            style={{ textAlign: "right", margin: 10 }}
            current={pagination.current}
            pageSize={pagination.pageSize}
            total={pagination.total}
            onChange={(page, pageSize) => {
              const p = {
                current: page,
                pageSize: pageSize,
                total: pagination.total,
              };
              getCollectionList(collectionName, p);
            }}
          />
          <Modal
            title="Upload File"
            visible={uploadModal}
            onOk={() => {
              getCollectionList(collectionName, pagination);
              setUploadModal(false);
            }}
            onCancel={() => setUploadModal(false)}
            destroyOnClose={true}
          >
            <Dragger {...props} className="upload-list-inline">
              <p className="ant-upload-drag-icon">
                <InboxOutlined />
              </p>
              <p className="ant-upload-text">
                Click or drag file to this area to upload
              </p>
              <p className="ant-upload-hint">
                Support for a single or bulk upload. Strictly prohibit from
                uploading company data or other band files
              </p>
            </Dragger>
          </Modal>
        </>
      )}
    </>
  );
}

export default FilesView;
