import * as actionTypes from "./actionTypes";
import { message } from "antd";

import { ICollection, CollectionAction, DispatchType } from "../types/global";
import {
  deleteCollection,
  getAllCollectionList,
  upsertCollectionData,
} from "../http";
import { AxiosResponse } from "axios";

export function addCollection(collection: ICollection) {
  const action: CollectionAction = {
    type: actionTypes.ADD_COLLECTION,
    collection,
  };
  return async (dispatch: DispatchType) => {
    await upsertCollectionData(collection);
    dispatch(action);
  };
}

export function removeCollection(collection: ICollection) {
  const action: CollectionAction = {
    type: actionTypes.REMOVE_COLLECTION,
    collection,
  };

  return async (dispatch: DispatchType) => {
    await deleteCollection(collection);
    dispatch(action);
  };
}

export function selectCollection(collection: ICollection) {
  const action: CollectionAction = {
    type: actionTypes.SET_COLLECTION,
    collection,
  };
  return disPatchAction(action);
}

export function updateCollection(collection: ICollection) {
  const action: CollectionAction = {
    type: actionTypes.UPDATE_COLLECTION,
    collection,
  };
  return async (dispatch: DispatchType) => {
    const key = "updatable";

    message.loading({ content: "Saving...", key });
    await upsertCollectionData(collection);
    message.success({ content: "Saved!", key, duration: 2 });

    dispatch(action);
  };
}

const disPatchAction = (collection: CollectionAction) => {
  return (dispatch: DispatchType) => dispatch(collection);
};

export const initData = () => async (dispatch: DispatchType) => {
  let { data: initialState }: AxiosResponse = await getAllCollectionList();
  const collection: CollectionAction = {
    type: actionTypes.INIT_COLLECTION,
    collections: initialState,
    collection: initialState[0] || {},
  };

  dispatch(collection);
};
