import { message } from "antd";
import axios, { AxiosResponse } from "axios";
import { ICollection } from "./types/global";

axios.interceptors.request.use(
  (config) => {
    if (localStorage.getItem("token"))
      config.headers.authorization = localStorage.getItem("token");
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

axios.interceptors.response.use(
  (response) => {
    if (response.status === 401) {
      message.error("Not authorized");
      window.location.replace("/admin-ui/login");
    }
    return response;
  },
  (error) => {
    if (error.response.status === 401) {
      message.error("Not authorized");
      return window.location.replace("/admin-ui/login");
    }
    if (error.response && error.response.data) {
      return Promise.reject(error.response.data);
    }
    return Promise.reject(error.message);
  }
);

export function getAllCollectionList(): Promise<AxiosResponse> {
  return axios.get("/admin/model/list");
}

export function upsertCollectionData(
  collection: ICollection
): Promise<AxiosResponse> {
  return axios.post("/admin/model/create/" + collection.name, collection);
}

export function deleteCollection({
  name,
}: ICollection): Promise<AxiosResponse> {
  return axios.delete("/admin/model/delete/" + name);
}

export function crudCollection(
  crudBody: ICurdBody,
  query: String = ""
): Promise<AxiosResponse> {
  return axios.post("/admin/model/crud/" + query, crudBody);
}

export enum EOperations {
  read = "read",
  list = "list",
  create = "create",
  update = "update",
  delete = "delete",
  deleteAll = "deleteAll",
  ref = "ref",
}

interface ICurdData {
  id?: string | Array<string>;
  query?: {
    pagination?: {
      current: number;
      pageSize: number;
    };
    find?: {};
    search?: string;
  };
  body?: {};
}

export interface ICurdBody {
  model: string;
  operations: EOperations;
  data: ICurdData;
}

export function getSwaggerJson(): Promise<AxiosResponse> {
  return axios.get("/admin/api-docs/editor");
}

export function loginAdmin(loginData): Promise<AxiosResponse> {
  return axios.post("/auth/login", loginData).then((response: any) => {
    const { data } = response;
    if (data.status) {
      localStorage.setItem("token", data.token);
      return response;
    }
    return response;
  });
}

export function registerAdmin(registerData): Promise<AxiosResponse> {
  return axios
    .post("/auth/admin/register", registerData)
    .then((response: any) => {
      const { data } = response;
      if (data.status) {
        localStorage.setItem("token", data.token);
        return response;
      }
      return response;
    });
}
