import { PageHeader, Tag, List } from "antd";
import { useHistory } from "react-router-dom";
import { shallowEqual, useSelector } from "react-redux";
import { CollectionsState, ICollection } from "../types/global";

function DataFlowPage() {
  const history = useHistory();

  const collections: ICollection[] = useSelector(
    (state: CollectionsState) => state.collections,
    shallowEqual
  ).filter((collection) => collection.show);

  return (
    <>
      <PageHeader
        title="Available Collections"
        subTitle="Select any collection to view data"
        ghost={false}
      />

      <div className="grid-responsive">
        {collections.map((collection) => (
          <Tag
            style={{
              paddingTop: 15,
              paddingBottom: 15,
              textTransform: "capitalize",
              margin: 5,
              letterSpacing: 1.2,
              cursor: "pointer",
              alignContent: "center",
              paddingLeft: 30,
              paddingRight: 30,
              fontWeight: 600,
              fontSize: 20,
            }}
            color="orange"
            onClick={() => {
              history.push(
                `/data/${collection.name}?action=list&page[number]=1&page[limit]=10`
              );
            }}
          >
            <span>{collection.name}</span>
          </Tag>
        ))}
      </div>
    </>
  );
}

export default DataFlowPage;
