import {
  PageHeader,
  Card,
  Input,
  Form,
  Button,
  Select,
  message,
  InputNumber,
  DatePicker,
} from "antd";
import {
  CollectionsState,
  ICollection,
  ICollectionSchema,
} from "../../types/global";
import { crudCollection, EOperations, ICurdBody } from "../../http";
import moment from "moment";
import RefSelect from "../DataFlow/RefSelect";
import UploadFile from "./UploadFile";
import JSONEditor from "./JSONEditor";
import { useHistory, useParams } from "react-router";
import { shallowEqual, useSelector } from "react-redux";
import queryString from "query-string";
import { useEffect, useState } from "react";

const { Option } = Select;

const getKeyValue =
  <U extends keyof T, T extends object>(key: U) =>
  (obj: T) =>
    obj[key];

interface IQueryParams {
  action?: string;
  id?: string;
}

function UpdateCollectionAction() {
  const history = useHistory();
  const [selectedRecord, setSelectedRecord] = useState<any>();
  const { collection: collectionName }: any = useParams();
  const queryParams: IQueryParams = queryString.parse(history.location.search);
  const selectedCollection: ICollection = useSelector(
    (state: CollectionsState) => state.collections,
    shallowEqual
  )
    .filter((collection) => collection.show)
    .find((c) => c.name === collectionName);

  const [form] = Form.useForm();

  const onFinish = (values: any) => {
    updateData(values);
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log("Failed:", errorInfo);
  };

  const updateData = async (formData: any) => {
    try {
      const body: ICurdBody = {
        model: selectedCollection?.name || "",
        operations: EOperations.update,
        data: {
          body: formData,
          id: queryParams.id,
        },
      };
      await crudCollection(body);
      message.success("Data Saved");
      window.history.back();
    } catch (error: any) {
      message.error(error.response.data);
      console.log(error);
    }
  };

  const getRecordData = async () => {
    try {
      const action: ICurdBody = {
        model: collectionName,
        operations: EOperations.read,
        data: {
          id: queryParams.id,
        },
      };
      const { data } = await crudCollection(action);

      setSelectedRecord(data);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (!selectedRecord && queryParams.id) {
      getRecordData();
    }
  }, [queryParams]);

  return (
    <>
      {selectedRecord && (
        <div>
          <PageHeader onBack={() => window.history.back()} title={"Edit"} />
          <Card
            style={{ marginLeft: 10, marginRight: 10 }}
            title={
              <span style={{ letterSpacing: 1.2, textTransform: "capitalize" }}>
                {selectedCollection?.name}
              </span>
            }
          >
            <Form
              name="basic"
              onFinish={onFinish}
              onFinishFailed={onFinishFailed}
              layout="vertical"
              size="large"
              form={form}
            >
              {selectedCollection.schema &&
                Object.keys(selectedCollection.schema).map((key: any) => {
                  const schemaValue = getKeyValue<
                    keyof ICollectionSchema,
                    ICollectionSchema
                  >(key)(selectedCollection.schema);

                  switch (schemaValue.type) {
                    case "string":
                      return (
                        <Form.Item
                          label={
                            <span
                              style={{
                                textTransform: "capitalize",
                                color: "#434343",
                              }}
                            >
                              {schemaValue.name}
                            </span>
                          }
                          name={schemaValue.name}
                          rules={[
                            {
                              required: schemaValue.required,
                              enum: ["male", "female"],
                            },
                          ]}
                          initialValue={selectedRecord[schemaValue.name]}
                        >
                          {schemaValue.enum.length ? (
                            <Select placeholder={`Select ${schemaValue.name}`}>
                              <>
                                {schemaValue.enum.map((e: any) => (
                                  <Option value={e}>{e}</Option>
                                ))}
                              </>
                            </Select>
                          ) : (
                            <Input />
                          )}
                        </Form.Item>
                      );

                    case "number":
                      return (
                        <Form.Item
                          label={
                            <span
                              style={{
                                textTransform: "capitalize",
                                color: "#434343",
                              }}
                            >
                              {schemaValue.name}
                            </span>
                          }
                          name={schemaValue.name}
                          rules={[
                            {
                              required: schemaValue.required,
                            },
                          ]}
                          initialValue={selectedRecord[schemaValue.name]}
                        >
                          <InputNumber style={{ width: 300 }} />
                        </Form.Item>
                      );
                    case "date":
                      return (
                        <Form.Item
                          label={
                            <span
                              style={{
                                textTransform: "capitalize",
                                color: "#434343",
                              }}
                            >
                              {schemaValue.name}
                            </span>
                          }
                          name={schemaValue.name}
                          rules={[
                            {
                              required: schemaValue.required,
                            },
                          ]}
                          initialValue={moment(
                            selectedRecord[schemaValue.name]
                          )}
                        >
                          <DatePicker />
                        </Form.Item>
                      );
                    case "boolean":
                      return (
                        <Form.Item
                          label={
                            <span
                              style={{
                                textTransform: "capitalize",
                                color: "#434343",
                              }}
                            >
                              {schemaValue.name}
                            </span>
                          }
                          name={schemaValue.name}
                          rules={[
                            {
                              required: schemaValue.required,
                            },
                          ]}
                          initialValue={selectedRecord[
                            schemaValue.name
                          ]?.toString()}
                        >
                          <Select placeholder={`Select`}>
                            <Option value={"true"}>True</Option>
                            <Option value={"false"}>False</Option>
                          </Select>
                        </Form.Item>
                      );
                    case "ref":
                      return (
                        <RefSelect
                          action={"update"}
                          selectedRecord={selectedRecord}
                          schemaValue={schemaValue}
                        />
                      );
                    case "file":
                      return (
                        <UploadFile
                          schemaValue={schemaValue}
                          form={form}
                          selectedRecord={selectedRecord}
                          action={"update"}
                        />
                      );
                    case "json":
                      return (
                        <JSONEditor
                          action={"update"}
                          selectedRecord={selectedRecord}
                          schemaValue={schemaValue}
                          form={form}
                        />
                      );

                    default:
                      return null;
                  }
                })}

              <Form.Item>
                <Button type="primary" htmlType="submit">
                  {"Update"}
                </Button>
              </Form.Item>
            </Form>
          </Card>
        </div>
      )}
    </>
  );
}

export default UpdateCollectionAction;
