import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
import { Layout, Alert, Button } from "antd";
import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { ErrorBoundary } from "react-error-boundary";

import { initData } from "./store/actionCreators";
// components
import Header from "./components/Header";
import SideBar from "./components/SideBar";

import CollectionsPage from "./pages/CollectionsPage";
import SchemaFlowPage from "./pages/SchemaFlowPage";
import DataFlowPage from "./pages/DataFlowPage";
import FilePage from "./pages/FilePage";
import AuthPage from "./pages/AuthPage";
import ApiDocsPage from "./pages/ApiDocsPage";
import LoginPage from "./pages/LoginPage";
import RegisterPage from "./pages/RegisterPage";
import DataFlowDetailPage from "./pages/DataFlowDetailPage";

const { Content } = Layout;

function ErrorFallback({ error, resetErrorBoundary }) {
  return (
    <Alert
      message="Something broke, please try again"
      description={error.message}
      type="error"
      action={
        <Button onClick={resetErrorBoundary} size="small" danger>
          Detail
        </Button>
      }
    />
  );
}

function App() {
  const isAuth = localStorage.getItem("token");

  return (
    <Layout className="main">
      <ErrorBoundary
        FallbackComponent={ErrorFallback}
        onReset={() => {
          // reset the state of your app so the error doesn't happen again
        }}
      >
        <Router basename="/admin-ui">
          {isAuth ? (
            AuthRoutes()
          ) : (
            <>
              <Route path="/register" component={RegisterPage} />
              <Route path="/login" component={LoginPage} />
              <Redirect path="/" to="/login" />
            </>
          )}
        </Router>
      </ErrorBoundary>
    </Layout>
  );
}

const AuthRoutes = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(initData());
  }, []);

  return (
    <>
      <Layout>
        <SideBar />
        <Layout>
          <Header />
          <Content style={{ overflow: "initial" }}>
            <Switch>
              <Route path="/" exact component={SchemaFlowPage} />
              <Route path="/collections" exact component={CollectionsPage} />
              <Route path="/data" exact component={DataFlowPage} />
              <Route
                path="/data/:collection"
                exact
                component={DataFlowDetailPage}
              />
              <Route path="/files" exact component={FilePage} />
              <Route path="/auth" exact component={AuthPage} />
              <Route path="/api-docs" exact component={ApiDocsPage} />
            </Switch>
          </Content>
        </Layout>
      </Layout>
    </>
  );
};

export default App;
