import { ICollection } from "../types/global";
import { useState } from "react";
import { CaretRightOutlined, CaretDownOutlined } from "@ant-design/icons";
function SchemaView({ collection }: { collection: ICollection }) {
  const dataSource = Object.keys(collection.schema).map((key, index) => ({
    key: index,
    field: key,
    type: collection.schema[key].type,
  }));

  const [collapsed, setCollapsed] = useState(true);

  return (
    <>
      <h2
        style={{
          backgroundColor: "#1890ff",
          textTransform: "capitalize",
          padding: 10,
          color: "white",
          letterSpacing: 1.5,
          display: "flex",
          justifyContent: "space-between",
          margin: 0,
        }}
      >
        {collection.name}
        <span
          style={{ cursor: collapsed ? "zoom-out" : "zoom-in" }}
          onClick={() => setCollapsed(!collapsed)}
        >
          {" "}
          {!collapsed ? <CaretRightOutlined /> : <CaretDownOutlined />}{" "}
        </span>
      </h2>
      {collapsed && (
        <table style={{ width: "100%", transition: "5s" }}>
          {dataSource.map((data) => (
            <tr key={data.key}>
              <td
                style={{
                  paddingLeft: 8,
                  textAlign: "left",
                  fontSize: 16,
                  letterSpacing: 1.2,
                }}
              >
                {data.field}
              </td>
              <td
                style={{
                  paddingRight: 8,
                  textAlign: "right",
                  fontSize: 14,
                  letterSpacing: 1.2,
                  color: "#8c8c8c",
                }}
              >
                {data.type}
              </td>
            </tr>
          ))}
        </table>
      )}
    </>
  );
}

export default SchemaView;
