import React, { useState, useEffect } from "react";
import { Select, Form } from "antd";
import { ICurdBody, crudCollection, EOperations } from "../../http";

const { Option } = Select;

function RefSelect({ schemaValue, action, selectedRecord }: { schemaValue: any, action: string, selectedRecord: any }) {
  const [data, setData] = useState<Array<any>>([]);
  const [search, setSearch] = useState<string>("");
  const [searchValues, setSearchValues] = useState<Array<string>>([]);
  const [value, setValue] = useState<Array<string>>([]);


  const makeSearch = async () => {
    try {
      const action: ICurdBody = {
        model: schemaValue.ref,
        operations: EOperations.ref,
        data: {
          query: {
            search: search,
          },
        },
      };
      const { data } = await crudCollection(action);
      setData(data.list);
      setSearchValues(data.searchValues);
    } catch (error) { }
  };

  const options = data.map((d) => (
    <Option value={d._id}>
      {searchValues.map((value) => (d[value]?.length ? `${d[value]}` : d._id))}
    </Option>
  ));

  useEffect(() => {
    makeSearch();
  }, [search]);

  return (
    <Form.Item
      label={
        <span
          style={{
            textTransform: "capitalize",
            color: "#434343",
          }}
        >
          {schemaValue.name}
        </span>
      }
      name={schemaValue.name}
      rules={[
        {
          required: schemaValue.required,
        },
      ]}

      initialValue={
        action === "update" && selectedRecord[schemaValue.name]
          ? !Array.isArray(selectedRecord[schemaValue.name]) ? [selectedRecord[schemaValue.name]._id] : selectedRecord[schemaValue.name].map(value => value._id || value)
          : ""
      }
    >
      <Select
        showSearch
        value={value}
        placeholder={`Search anything in ${schemaValue.name}`}
        defaultActiveFirstOption={true}
        showArrow={false}
        filterOption={false}
        onSearch={(s) => setSearch(s)}
        onChange={(v) => setValue(v)}
        notFoundContent={null}
        mode={schemaValue.relationship === "hasmany" ? "multiple" : undefined}
      >
        {options}
      </Select>
    </Form.Item>
  );
}

export default RefSelect;
