import { useLocation } from "react-router-dom";
import { PageHeader } from "antd";

function AppHeader() {
  const location = useLocation();
  const headerNames: any = {
    "": "Schema Flow",
    collections: "Schema",
    data: "Data Flow",
  };
  const subHeaderNames: any = {
    "": "Visualize you database",
    collections: "Manage your mongoose schema",
    data: "Manage your data",
  };

  const headerName = headerNames[location.pathname.split("/")[1]];

  const subHeaderName = subHeaderNames[location.pathname.split("/")[1]];

  if (headerName === "Data Flow") return null;
  return (
    <>
      {headerName && <PageHeader ghost={false} title={headerName} subTitle={subHeaderName} />}
    </>
  );
}

export default AppHeader;
