import { useEffect, useState } from "react";
import ReactFlow from "react-flow-renderer";
import { useSelector } from "react-redux";
import { CollectionsState, ESchemaTypes, ICollection } from "../types/global";
import { upsertCollectionData } from "../http";

import SchemaView from "../components/SchemaView";

const SchemaFlowPage = () => {
  const collections: ICollection[] = useSelector(
    (state: CollectionsState) => state.collections
  );

  const [collectionNodes, setCollectionNodes] = useState([]);

  const checkIfRefExists = (
    schema: any,
    collectionName: string,
    id: string
  ) => {
    let elementsNodes: any = [];
    Object.keys(schema).forEach((key, index) => {
      if (schema[key].type === ESchemaTypes.ref) {
        elementsNodes.push({
          id: `e1-${id}-${index}-${key}`,
          source: collectionName,
          target: schema[key].ref,
          type: "smoothstep",
          label: `${collectionName} ${schema[key].relationship} ${schema[key].ref}`,
          animated: false,
          labelStyle: {
            textTransform: "lowercase",
            fontSize: 20,
            letterSpacing: 1.2,
            backgroundColor: "#ff9c6e",
          },
          style: {
            width: "100%",
            padding: 20,
            borderRadius: 5,
          },
        });
      }
    });

    return elementsNodes;
  };

  const mapNodes = () => {
    let nodes: any = [];

    collections.forEach((collection: ICollection, index) => {
      nodes.push({
        id: collection.name,
        data: {
          label: (
            <div>
              <SchemaView collection={collection} />
            </div>
          ),
        },
        style: {
          padding: 0,
          width: "300px",
          borderColor: "#40a9ff",
          borderRadius: 0,
        },
        position: { x: collection.x, y: collection.y },
      });
      nodes.push(
        ...checkIfRefExists(collection.schema, collection.name, collection.id)
      );
    });

    setCollectionNodes(nodes);
  };

  useEffect(() => {
    mapNodes();
  }, [collections]);

  const updateCollectionPosition = async (id: string, x: number, y: number) => {
    let collection: ICollection | undefined = collections.find(
      (collection) => collection.name === id
    );
    if (!collection) return;
    try {
      collection.x = x;
      collection.y = y;
      await upsertCollectionData(collection);
    } catch (error) {
      console.log(error);
    }
  };

  const position = localStorage.getItem("displayPosition")
    ? JSON.parse(localStorage.getItem("displayPosition"))
    : JSON.parse(`{"x":100,"y":100,"zoom":1}`);

  return (
    <div
      style={{
        width: "100%",
        height: "100vh",
        overflow: "auto",
      }}
    >
      <ReactFlow
        defaultPosition={[position.x, position.y]}
        elements={collectionNodes}
        minZoom={0.4}
        defaultZoom={position.zoom}
        snapToGrid={true}
        snapGrid={[15, 15]}
        onlyRenderVisibleElements={true}
        onMove={(e) => {
          localStorage.setItem("displayPosition", JSON.stringify(e));
        }}
        onNodeDragStop={(event, node) => {
          updateCollectionPosition(node.id, node.position.x, node.position.y);
        }}
      />
    </div>
  );
};

export default SchemaFlowPage;
