import React, { useState } from "react";
import { Layout, Menu, Divider, Button, Space, Affix } from "antd";
import {
  AlignCenterOutlined,
  LineChartOutlined,
  DatabaseOutlined,
  FileOutlined,
  ApiTwoTone,
  ApiOutlined,
  UserSwitchOutlined,
  SlidersTwoTone,
  SlidersOutlined,
  CodeSandboxOutlined,
  CodeSandboxCircleFilled,
  PoweroffOutlined,
  MenuUnfoldOutlined,
  MenuFoldOutlined,
} from "@ant-design/icons";
import { Link, useLocation } from "react-router-dom";

const { Sider } = Layout;

function SideBar() {
  const location = useLocation();

  const [collapsed, setCollapsed] = useState(true);

  const selectedMenu = location.pathname.split("/")[1];

  const pages = [
    {
      key: "",
      to: "/",
      name: "Schema Flow",
      icon: <LineChartOutlined />,
    },
    {
      key: "collections",
      to: "/collections",
      name: "Collections",
      icon: <AlignCenterOutlined />,
    },
    {
      key: "data",
      to: "/data",
      name: "Data Flow",
      icon: <DatabaseOutlined />,
    },
  ];

  const plugins = [
    {
      key: "files",
      to: "/files",
      name: "Files",
      icon: <FileOutlined />,
    },
    {
      key: "auth",
      to: "/auth",
      name: "Auth",
      icon: <UserSwitchOutlined />,
    },
  ];

  return (
    <>
      <Sider
        width={300}
        collapsed={collapsed}
        theme="light"
        onCollapse={(collapsed, type) => {
          setCollapsed(collapsed);
        }}
        style={{
          height: "100vh",
          position: "fixed",
          left: 0,
          zIndex: 100,
          display: !collapsed ? "" : "none",
          boxShadow: "10px 1px 19px -7px rgba(0,0,0,0.97)",
        }}
      >
        <Menu
          style={{ marginTop: 10 }}
          mode="inline"
          defaultSelectedKeys={[selectedMenu]}
          onClick={() => setCollapsed(true)}
        >
          <Divider orientation="left">
            {collapsed ? (
              <CodeSandboxCircleFilled />
            ) : (
              <Space>
                <CodeSandboxOutlined /> Core
              </Space>
            )}
          </Divider>
          {pages.map((page) => (
            <Menu.Item key={page.key} icon={page.icon}>
              <Link to={page.to}>{page.name}</Link>
            </Menu.Item>
          ))}

          <Divider orientation="left">
            {collapsed ? (
              <ApiTwoTone />
            ) : (
              <Space>
                {" "}
                <ApiOutlined /> Plugins{" "}
              </Space>
            )}
          </Divider>
          {plugins.map((plugin) => (
            <Menu.Item key={plugin.key} icon={plugin.icon}>
              <Link to={plugin.to}>{plugin.name}</Link>
            </Menu.Item>
          ))}
        </Menu>

        <Divider orientation="left">
          {collapsed ? (
            <SlidersTwoTone />
          ) : (
            <Space>
              <SlidersOutlined /> General
            </Space>
          )}
        </Divider>

        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "start",
          }}
        >
          <Button
            type="link"
            onClick={() => {
              window.open("/admin/api-docs/", "_blank").focus();
            }}
          >
            {collapsed ? <b>API</b> : "Api Documentation"}
          </Button>

          <Button
            type="link"
            onClick={() => {
              localStorage.clear();
              window.location.replace("/admin-ui");
            }}
          >
            {collapsed ? <PoweroffOutlined /> : "Logout"}
          </Button>
        </div>
      </Sider>

      <button
        style={{ position: "fixed", bottom: 10, left: 10, zIndex: 200 }}
        className="menu-button"
        onClick={() => setCollapsed(!collapsed)}
      >
        <Space>
          <img width="70" src={"/img/white-logo.png"} alt=""></img>
          {collapsed ? (
            <MenuUnfoldOutlined size={25} />
          ) : (
            <MenuFoldOutlined size={25} />
          )}
        </Space>
      </button>
    </>
  );
}

export default SideBar;
