import { Tag } from "antd";

import { ESchemaTypes } from "../types/global";

function Badge({ type }: { type: any }) {
  type = type.toLowerCase();
  const getBadgeColor = () => {
    switch (type) {
      case ESchemaTypes.string:
        return "#f50";
      case ESchemaTypes.number:
        return "#d4b106";
      case ESchemaTypes.boolean:
        return "#52c41a";
      case ESchemaTypes.ref:
        return "#13c2c2";
      case ESchemaTypes.date:
        return "#1890ff";
      case ESchemaTypes.file:
        return "#2f54eb";
      case ESchemaTypes.json:
        return "#531dab";
      default:
        return "#8c8c8c";
    }
  };

  const getBadgeName = () => {
    switch (type) {
      case ESchemaTypes.string:
        return "Abc";
      case ESchemaTypes.number:
        return "123";
      case ESchemaTypes.boolean:
        return "true";
      case ESchemaTypes.ref:
        return "Relation";
      case ESchemaTypes.date:
        return "17 Aug";
      case ESchemaTypes.file:
        return "File";
      case ESchemaTypes.json:
        return "JSON";
      default:
        return "NA";
    }
  };

  return (
    <div>
      <Tag color={getBadgeColor()} style={{ fontWeight: "bold" }}>
        {getBadgeName()}
      </Tag>
    </div>
  );
}

export default Badge;
